.app {
  text-align: center;
}

.books-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.book {
  margin: 20px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 200px;
}

.book-image {
  width: 150px;
  height: 150px;
  object-fit: cover;
}

button {
  margin-top: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
